.popUp {
  position: absolute;
  background-color: rgb(96, 123, 133);

  width: 94vw;
  height: 94.5vh;

  max-width: calc(100vh * 1.43);
  max-height: 94.35vh;//calc(100vw / 1.56);

  max-width: 149vh;
  max-height: 63.5161290323vw;
  
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  
  z-index: 200;
  transition: 0.5s;

  border: 2px dashed #485563;
  border-radius: 20px;

  /* left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); */
  animation: 0.5s appear;
  margin: 0 auto;
}

@keyframes appear {
  from {
//height: 0vh;
    opacity: 0;
  }
  to {
//height: 94vh;
    opacity: 1;
  }
}
  
.popUp_none {
  
  opacity: 0;
  height: 0;
  border: 2px dashed #485563;
  transition: 0.5s;
} 

.buttons {
  display: flex;
  justify-content: space-between;
  gap:2vw;
}



.ok:hover,
.cancel:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
} 


.close_modal,
.info {
  width: 6vw;
  height: 8vh;
  //margin: 0.5vw 0 0 82vw;
  width: fit-content;
  height: fit-content;
  padding: 1vh 1vw 0 1vw;
  margin: 1vh 1vw;
  font-size: 7vh;
  cursor: pointer;
  border: 2px solid #485563;
  background-color: #669999;
  border-radius: 10px;

  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    //box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);

    transition: 0.3s;
}

.inf {
  width: 6vw;
  height: 8vh;
  //margin: 0.5vw 0 0 82vw;
  width: fit-content;
  height: fit-content;
  padding: 0vh 1.3vw 0 1.3vw;
  margin: 1vh 1vw;
  font-size: 7vh;
  opacity: 1;
  border: 2px solid #485563;
  /* background-color: #669999; */
  border-radius: 50%;
}

.close_modal:hover,
.info:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
}

