
.menu_active {
  position: absolute;
  z-index: 2;
  //width: fit-content;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid rgba(0, 0, 0, 0.63);

  color: whitesmoke;
  background: linear-gradient(90deg, #29323c 0%, #485563 100%);

  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));


  border-radius: 10px;
  padding: 0.2rem;

}

