.menu {
  display: none;
  z-index: 0;

}

.start {
  text-align: center;
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));

  font-size: calc(0.8vw + 3vh); //2rem
  border: 2px dashed lightblue;
  margin: 0.5rem;
  padding: 0.3rem;
  border-radius: 0.5rem;
  transition: 0.5s;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  color: white;
  background: linear-gradient(90deg, #29323c 0%, #485563 100%);
}

.start_off {
  opacity: 0.5;
  pointer-events: none;
}

.start:hover {

  transition: 0.5s;
  cursor: pointer;
  background-color: linear-gradient(90deg, #29323c 0%, #485563 100%);
  
  color: #c4c5c7;
  box-shadow: none;
  transition: 0.5s;
  
}

// settings

.settings {
  padding: 10px;
}

.settings_container{
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.sounds,
.music {
  width: 6.5em;
  border: 2px dashed rgb(91, 136, 136);
  border-radius: 10px;
  margin: 12px 0;
}

.rank1 {
  color: black;
  font-size: medium;
  background-color: gold;
  //border: 1px solid black;
  border-radius: 3px;

  box-shadow: 0 0 5px 2px rgb(97, 97, 97);
}


.rank2 {
  font-size:small;
  background-color: silver;
  //border: 1px solid black;
  color: black;

  border-radius: 3px;

  box-shadow: 0 0 5px 2px rgb(97, 97, 97);
}

.rank3 {
  font-size: small;
  background-color: #cd7f32;
  color: black;

  border-radius: 3px;

  box-shadow: 0 0 5px 2px rgb(97, 97, 97);
}

.ranks {
  font-size: small;
}

.lang {
  display: flex;
  flex-direction: row;
  justify-content: center;

  font-size:small;
  
}

.active {
  color: lightblue;
