.attack_container {
  display: flex;
  flex-direction: column;
  //margin-top: -10px;


  flex-wrap: wrap;

}

.table_army {
  display: flex;
flex-direction: row;
}

.line_army {

}


.name_army{
  font-size: 3.5vh;
  margin-bottom: 1vh;
}

.value {
  display: inline-block;
  font-size: 3.5vh;
  padding: 0 1.2vh;
  flex-wrap: nowrap;
  //width: 12vw;

}

.buttons {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  
  padding: 1vh;
  gap:2vw;
}

.ok,
.cancel {
  width: calc(10vw * 2);
  height: 10vh;//calc(100vh * 0.01);

  //min-height: 75px;

  font-size: 7vh;
  color: white;



  background-color: transparent;
  border: 1px solid black;
  border-radius: 10px;

  cursor: pointer;

  border: 2px solid #485563;
  background-color: #669999;
  border-radius: 10px;

  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);
  
    transition: 0.3s;
}

.ok:hover,
.cancel:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
}


.result {
  position: absolute;
  width: 95vw;
  height: 94vh;
}
