.defmodal {
  position: absolute;

  display: flex;
  flex-direction: column;
  justify-content: center;
/*   top: 10vh;
  left: 25vh; */
  width: 50vw;
  height: calc(100vw / 2.5);

  background-color: gray;
  border: 1px solid black;
  border-radius: 20px;

  font-size: calc(1.8vw + 1.9vh);
  font-size: 4vh;

  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);

  z-index: 50;


  width: 60vw;
  height: 55.5vh;

  max-width: calc(100vh * 1.49);
  max-height: 94.35vh;//calc(100vw / 1.56);
  
  
  margin-left: auto;
  margin-right: auto;
  top:10%;
  left: 0;
  right: 0;
  text-align: center;

}

.table {
  display: flex;
  flex-direction: column;
  align-items: left;

  margin-top: 1vh;
}


.army_type {
  position: relative;
  bottom: 5px;
  color: lightgreen;
}

.btn_ok {
  width: 25vw;
  height: 12vh;
  
  margin: 2vh auto;
  border-radius: 20px;
}
