.popUp {
  position: absolute;
  background-color: rgb(96, 123, 133);

  width: 94vw;
  height: 94vh;

  max-width: calc(100vh * 1.43);
  max-height: 94.35vh;//calc(100vw / 1.56);

  max-width: 149vh;
  max-height: 63.5161290323vw;
  
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  
  z-index: 200;
  transition: 0.5s;

  border: 2px dashed #485563;
  border-radius: 20px;

  /* left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); */
  animation: 0.5s appear;
  margin: 0 auto;
}

.buttons {
  display: flex;
  justify-content: space-between;
  gap:2vw;
}



.ok:hover,
.cancel:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
} 


.close_modal,
.info {
  width: 6vw;
  height: 8vh;
  //margin: 0.5vw 0 0 82vw;
  width: fit-content;
  height: fit-content;
  padding: 2vh 2vw;
  padding: 1vh 1vw 0 1vw;
  margin: 1vh 1vw;
  font-size: 7vh;
  cursor: pointer;
  border: 2px solid #485563;
  background-color: #669999;
  border-radius: 10px;

  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    //box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);

    transition: 0.3s;
}

.info {
  opacity: 0;
}

.close_modal:hover,
.info:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
}

.title {
  height: 16vh;
  font-size: 6vh;
  white-space: normal;
}

.slide {
  position: relative;
  width: 90%;
  margin: 0 auto;
  height: 55%;
}

.firstimg {
  display: block;
  //width: 90%;
  max-width: 100%;
  max-height: 100%;

  margin: 0 auto;
  border-radius: 10px;
}



.content {
  display: flex;
  
  flex-direction: column;
  justify-content: left;
  align-items: flex-start;
  margin: calc(2vh + 2vw);
  width: 90%;

}

.row {
  margin: 2px;
  font-size: calc(1.5vw + 1.5vh)//1em;
}

.btn_ok {
  /* position: relative;
  bottom: 10px; */
  /* display: flex;
  gap:1px; */
  width: 25vw;
  height: 12vh;
  max-width: 160px;
  
  margin: 0 0.5vw;
  border-radius: 20px;

  //font-size: 1em//calc(1.5vw + 1.5vh);
  font-size: calc(1.2vw + 1.2vh);
}
