.info {
  position: absolute;

  display: flex;
  flex-direction: column;
  //justify-content: center;
/*   top: 10vh;
  left: 25vh; */
  width: 50vw;
  height: calc(100vw / 2.5);

  background-color: gray;

  border: 1px solid black;
  border-radius: 20px;

  font-size: calc(1.8vw + 1.9vh);
  font-size: 4vh;

  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);


  width: 60vw;
  height: 70.5vh;
  //height: fit-content;

  max-width: calc(100vh * 1.49);
  max-height: 94.35vh;//calc(100vw / 1.56);
  
  margin-left: auto;
  margin-right: auto;
  top:10%;
  left: 0;
  right: 0;
  text-align: center;
  
  z-index: 200;
}

.info_v {
  position: absolute;
  top:0;
  height: 100vh;
  width: 100vw;
}

.infogo {
  position: absolute;

  display: flex;
  flex-direction: column;
  justify-content: center;
/*   top: 10vh;
  left: 25vh; */
  width: 50vw;
  height: calc(100vw / 2.5);

  background-color: gray;
  background-color: rgb(164,164,164);
  border: 1px solid black;
  border-radius: 20px;

  font-size: calc(1.8vw + 1.9vh);
  font-size: 4vh;

  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);

  z-index: 50;


  width: 60vw;
  height: 70.5vh;

  max-width: calc(100vh * 1.49);
  max-height: 94.35vh;//calc(100vw / 1.56);
  
  margin-left: auto;
  margin-right: auto;

  left: 0;
  right: 0;
  text-align: center;
  z-index: 200;

  height: 100vh;
  width: 100vw;

  

}

.btn_ok {
  width: 20vw;
  height: 12vh;
  
  margin: 2vh auto;
  border-radius: 20px;
}

.victory {
  width: 15vw;
  height: 35vh;
  margin: 0 auto;
}

.gameover {
  width: 20vw;
  height: 20vh;
  margin: 0 auto;

}
