html, body {
  margin: 0;
  padding: 0;
  background: linear-gradient(90deg, #29323c 0%, #485563 100%);
  background-image: url("./assets/back8_upd.JPG"); //6,8,9
  background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    box-sizing: border-box;
}

#root {
  color: rgb(209, 209, 209);
}

.app {
  font-family: sans-serif;
  text-align: center;
  align-items: center;
}

.title_vert {
  display: none;
  margin: 45vh auto;
  text-align: center;
  //vertical-align: middle;

  text-shadow: -3px -3px 1.5px #dddddd;
    //background: radial-gradient(#ffffff 1px, transparent 1px);
    background-size: 4px 4px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@media (orientation: portrait) {
  .app {
    display: none;
  }
  .title_vert {
    display: block;
  }

}

.map_container {
  //display: flex;
  justify-content: center;
  flex-direction: row;
  perspective: calc(100vh / 2 + 10vw); //500px;
  
  margin: 1vh 0 1vh 0;
  height: 97vh;
  align-items: center;
}

.map_container_flex {
  display: flex;
}


.title {
  position: absolute;
  font-size: calc(3.5vw + 3.5vh);//3rem;
  //top: 3%; 
  right: 50%;
  transform: translate(50%,0%);
  //z-index: 50;
  color: rgba(255, 255, 255, 0.863);
  //white-space: nowrap;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  
  /* animation: color-change 3s 1; */

  text-shadow: -3px -3px 3px #dddddd;
    background: radial-gradient(#ffffff 1px, transparent 1px);
    background-size: 4px 4px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: table;
    margin: 0 auto;

    z-index: 600;

}

/* @keyframes color-change {
  0% { color: rgba(255, 255, 255, 0.719); }
  50% { color: rgba(141, 46, 46, 0.932); }

  100% { color: rgba(255, 255, 255, 0.719); }
} */

.title_none {
  display: none;
}


.map_active {
  -webkit-filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  border-radius: 5px;
  position: relative;
  transition: 1s;
  top:0%;
  bottom:0%;
  //right: 2.6%;
  max-height: 96vh;
}

.map {
  -webkit-filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .7));
  border-radius: 5px;
  transform: rotateX(45deg);
  position: relative;
  transition: 1s;
    //margin-top: -50px;
  pointer-events: none;
  bottom: calc(100vw * 0.1);
  //right: 2%;
  top:-20%;

  height: 96vh;

  
}

.fire {
  position: absolute;
  width: 45px;
  height: 45px;
  bottom: 70vh;
  left: 73vw;
  z-index: 300;
  //bottom: 50vh;
  opacity:1;
  transition: 3s;
  //transition-delay: 1s;
}

.fire_off {
  position: relative;
  width: 0;
  height: 0;
/*   transition: 2s;
  transition-delay: 1s; */
  //display: none;
  opacity: 0;
}
