.defmodal {
  position: absolute;

  display: flex;
  flex-direction: column;
  justify-content: center;
/*   top: 10vh;
  left: 25vh; */
  width: 50vw;
  height: calc(100vw / 3.5);

  background-color: gray;
  border: 1px solid black;
  border-radius: 20px;

  font-size: calc(1.8vw + 1.9vh);
  font-size: 4vh;

  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);

  z-index: 50;


  width: 94vw;
  height: 94.5vh;

  max-width: calc(100vh * 1.43);
  max-height: 94.35vh;//calc(100vw / 1.56);
  max-height: 63.5161290323vw;
  
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

.losses_table {
  display: flex;
  justify-content: center;
  gap:2vw;
  padding: 1vh;
}

.you_city, .opponent_city {
  //padding-top: 1vh;
  text-align: center;
}

.you_city {
  color: lightgreen;
}
.opponent_city {
  color: lightcoral;
}

.you, .opponent {
  text-align: left;
}

.btn_ok {
  width: 20vw;
  height: 8vh;
  
  margin: 2vh auto;
  border-radius: 10px;
}

.btn_speech {
  font-size: 2.5vh;
}

.buttons {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  gap:1vw;
}
