.main_container {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  
}

.line_army {

}

.name_army{
  font-size: calc(1.5vh + 1.5vw);//3.5vh;
  //margin-bottom: 1vh;
}

.values {
  display: inline-block;
  font-size: calc(1.5vh + 1.5vw);//calc(100vh * 0.04);
  padding: 0 0/* calc(100vh * 0.012) */;
  flex-wrap: nowrap;
  width: 10vw;
}

input[type=range] {
  -webkit-appearance: none;
  background-color: silver;
  //width: calc(100vh / 1.2);
  width: calc(20vw + 20vh);
  height: calc(1vh + 1vw) ;
  cursor: pointer;
  border-radius: 20px;
  margin-bottom: 2vh;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background-color: #669999;
  width: 6vh;
  height: 6vh;
  border: 2px dashed #485563;
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);
  transform: rotate(95deg);;
  border-radius: 50%;
}

.buttons {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  
  padding: calc(1.2vh + 1.2vw);
  gap:2vw;
}

.ok,
.cancel {
  width: calc(10vw * 1.5);
  height: 2em;//calc(3vw + 3vh);//10vh;//calc(100vh * 0.01);

  //min-height: 75px;

  font-size: calc(2vh + 2vw);
  color: white;



  background-color: transparent;
  border: 1px solid black;
  border-radius: 10px;

  cursor: pointer;

  border: 2px solid #485563;
  background-color: #669999;
  border-radius: 10px;

  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.25), 0 6px 6px rgba(0, 0, 0, 0.22);
  
    transition: 0.3s;
}

.ok:hover,
.cancel:hover {
  transform: translateY(-0.3vh);
  transition: 0.3s;
  box-shadow:none;
  filter: none;
}
  



