.interface {
  position: absolute;
  width: 95vw;
  height: 95vh;

  max-width: calc(100vh * 1.5);
  max-height: calc(100vw / 1.55);
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  //border: 1px solid #000000;
  //background-color: #29323c34;

  color: white;
  transition: 1s;
  //transition-delay: 1s;
 
  //z-index: 5;
  //aspect-ratio: 1.5/1;
}

.interface_none {
  opacity: 0;
}

/* .interface:hover {
  //z-index: 20;
  //z-index: 10;
} */

.interface_header,
.interface_footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  //border: 1px solid black;
  align-items: flex-start;
}

.interface_footer {
  align-items: flex-end;
  margin: -4px;
}

.interface_some {
  width: 40vw;
  height: calc(1.2vh + 1.2vw);

  max-width: calc(100vh * 0.75);

  font-size: calc(1vw + 0.5vh);
  text-align: center;
  padding: 0.5vh 0 0 1vh;

  //border: 3px dashed rgb(73, 73, 73);
  border-radius: 10px;
  background-color: rgba(96, 123, 133, 0.568);

  z-index: 20;
}

.interface_russia {
  display: flex;
  flex-direction: column;
  
  width: 28vw;
  height: 30vh;

  max-height: calc(30vw * 0.65);
  max-width: calc(30vh / 0.7);

 //border: 1px solid black;
  border-radius: 20px 20px 0 60%;
  z-index: 20;

  font-size: calc(10vw * 0.11 + 10vh * 0.09);
  cursor: default;
  border-radius: 20px 20px 0 60%;
  background: linear-gradient(to bottom, rgba(96, 123, 133, 0.568), rgba(0, 0, 0, 0));
}

.uk_name, .ru_name {
height: calc(2.5vw + 2.5vh);
}

.ru_panel {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  gap:0.8vw;
}

.uk_panel {
  display: flex;
  flex-direction: reverse;
  justify-content: center;
  gap:0.8vw;
}

.ru_stat {
 display: flex;
 flex-direction: column;
   width: fit-content;
  text-align: left;
}

.army_type {
  position: relative;
  bottom: 7.1px;
}

.controls {
  overflow:visible;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.btn_scout {
  width: 5.5em;//4.825em;
  height: 2.8em;
  
  border: none;
  border-radius: 10px;

  cursor: pointer;
  color: white;

  background:rgb(96, 123, 133);
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  box-shadow: 0 0px 0px rgba(0,0,0,0.25), 0 6px 6px rgba(0,0,0,0.22);

  border: 3px dashed rgb(146, 146, 146);

  //transform: translateY(0px);
  
  transition: 0.3s;
}

.btn_go, .btn_attack  {
  width: 4.825em;
  height: 2.8em;
  
  border: none;
  border-radius: 10px;

  cursor: pointer;
  color: white;

  background:rgb(96, 123, 133);
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  box-shadow: 0 0px 0px rgba(0,0,0,0.25), 0 6px 6px rgba(0,0,0,0.22);

  border: 3px dashed #1d6b21;

  //transform: translateY(0px);
  transition: 0.3s;
}

.btn_attack {
  border: 3px dashed #af3f3fd5;
}

button[disabled]{
  opacity: 0.3;
}


.btn_go:active  {
  transition: 0.3s;
  /* transform: translateY(-1vh); */
  
}

.btn_go_active {
  background-color: rgb(81, 103, 110);
  border: 2px dashed black;
  box-shadow: none;
  filter:none;
  scale: 0.99;
}


