.interface_russia_add {
  display: flex;
  justify-content: flex-end;
  //align-items: end;
  flex-direction: column;

  gap:2vh;
  width: 28vw;
  height: 35vh;


  max-height: calc(30vw * 0.45);
  max-width: calc(33vh / 1);

  //border: 1px solid black;

  font-size: calc(1.1vw + 1.1vh);
  
  z-index: 20;

  
  align-items: flex-end;
  //justify-content: end;
  padding-bottom: 3vh;

}

.spec, 
.last {
  display: flex;
  //width: 7.825em;
  height: 3em;
  align-items: center;
  margin-right: 1vw;
  //margin: 0 auto;

  //width: 4.825em;
  //height: 2.8em;
  
  border: none;
  border-radius: 10px;

  cursor: pointer;
  color: white;

  background:rgb(96, 123, 133);
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  box-shadow: 0 0px 0px rgba(0,0,0,0.25), 0 6px 6px rgba(0,0,0,0.22);

  border: 3px dashed #1d6b21;
}

.last {
  border: 3px dashed #af3f3fd5;

}

.interface_ukraine {
  display: flex;
  flex-direction: column;
 
  width: 30vw;
  height: 37vh;

  max-height: calc(40vw * 0.65);
  max-width: calc(40vh / 0.8); //0.7


  //border: 1px solid black;
  

  font-size: calc(10vw * 0.11 + 10vh * 0.09);
  cursor: default;

  z-index: 20;
  border-radius: 20px 20px 20px 20px;
  background: linear-gradient(to bottom, rgba(151, 100, 50, 0.568),rgba(0, 0, 0, 0));
}

.backtomenu {
  position: absolute;
  border-radius: 10px; 
  border: 2px dashed rgb(91, 136, 136);
  margin-top: 0.5em;
  background-color: rgba(80, 120, 120, 0.514);
  width: 4em;
  top: 5.5em;
  border: 2px dashed rgba(0, 0, 0, 0.404);
}

.backtomenu_img {
  width: 3em;//3rem;
  height: 3em;//3rem;
}

.education {
  position: absolute;
  //top: 5.5em;
  border-radius: 10px; 
  border: 2px dashed #1d6b21;
  margin-top: 0.5em;
  background-color: rgba(80, 120, 120, 0.897);
