.endstep_container {
  
  font-size: calc(1.1vw + 0.9vh);
  z-index: 20;
  filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.7));
  //box-shadow: 0 0px 0px rgba(0,0,0,0.25), 0 6px 6px rgba(0,0,0,0.22);
  padding-bottom: 8px;
  margin: 0 5vw 0 0;
}

.endstep {
  width: 5.5em;
  height: 2.8em;
  
  border: none;
  border-radius: 10px;

  cursor: pointer;
  color: rgb(197, 197, 197);

  background:rgb(96, 123, 133);
  //filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  box-shadow: 0 0px 0px rgba(0,0,0,0.25), 3px 3px 3px rgba(0,0,0,0.22);

  border: 2px solid #a5a5a5;

  transition: .3s;
  z-index: 20;
}

.endstep:hover {
  color: rgb(235, 235, 235);
  border: 2px solid #e0e0e0;
  transition: .3s;
}

.endstep:active {
  transition: 0.3s;
}
